.pipeline-icon-toolbar {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  padding: 20px;
  list-style: none; }

.pipeline-icon-button {
  position: relative;
  margin: 0;
  margin-bottom: 0.8em;
  padding: 0.8em;
  color: inherit;
  background: none;
  border: none;
  box-shadow: none;
  cursor: pointer;
  user-select: none; }

.kui-theme--light .pipeline-icon-button,
.kui-theme--light .pipeline-icon-button span {
  background: rgba(238, 238, 238, 0.9); }

.kui-theme--dark .pipeline-icon-button,
.kui-theme--dark .pipeline-icon-button span {
  background: rgba(26, 39, 48, 0.9); }

.pipeline-icon-button:focus {
  outline: 3px solid rgba(0, 191, 255, 0.85); }

[data-whatinput='mouse'] .pipeline-icon-button:focus {
  outline: none; }

.pipeline-icon-button span {
  position: absolute;
  right: 100%;
  margin: -0.3em 0;
  padding: 0.3em 0.4em;
  font-size: 1.4em;
  white-space: nowrap;
  transform: translateX(2em);
  visibility: hidden;
  opacity: 0;
  transition: all ease 0.2s, width 0s; }

.pipeline-icon-button:hover span,
[data-whatinput='keyboard'] .pipeline-icon-button:focus span {
  transform: translateX(0);
  visibility: visible;
  opacity: 1; }

.pipeline-icon-button svg {
  position: relative;
  display: block;
  width: 2em;
  height: 2em; }

.pipeline-icon-modal .kui-button {
  margin: 0 10px; }
