.pipeline-sidebar {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  padding-top: 12px;
  transform: translateX(-100%);
  transition: transform ease 0.4s; }

.pipeline-sidebar .pipeline-ui {
  visibility: hidden;
  transition: visibility 0.3s; }

@media (min-width: 480px) {
  .pipeline-sidebar {
    width: 300px;
    transform: translateX(-300px); } }

.pipeline-sidebar--visible {
  transform: translateX(0); }

@media (min-width: 480px) {
  .pipeline-sidebar--visible {
    transform: translateX(0); } }

.pipeline-sidebar--visible .pipeline-ui {
  visibility: visible;
  transition: visibility 0s; }

.kui-theme--light .pipeline-sidebar {
  background: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.07); }

.kui-theme--dark .pipeline-sidebar {
  background: #20303c;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3); }

.pipeline-sidebar__show-menu {
  position: absolute;
  top: 20px;
  left: 20px;
  transition: all ease 0.4s; }

.pipeline-sidebar__hide-menu {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 1; }

@media (min-width: 480px) {
  .pipeline-sidebar__hide-menu {
    right: -50px;
    z-index: auto; } }

.pipeline-ui {
  display: flex;
  flex-direction: column;
  height: 100%; }

.pipeline-sidebar__icon-button {
  width: 3.2em;
  height: 3.2em;
  margin: 0;
  padding: 4px;
  font-size: 1em;
  background: none;
  border: none;
  border-radius: 50%;
  box-shadow: none;
  transform: scale(1.3);
  visibility: hidden;
  cursor: pointer;
  opacity: 0;
  transition: all ease 0.1s; }

.pipeline-sidebar__icon-button--visible {
  visibility: visible;
  opacity: 1;
  transition: all 0.1s, visibility 0s; }

.pipeline-sidebar__icon-button:hover {
  transform: scale(1.5); }

.pipeline-sidebar__icon-button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(0, 191, 255, 0.85); }

[data-whatinput='mouse'] .pipeline-sidebar__icon-button:focus {
  box-shadow: none; }
